@font-face {
	font-family: "Didona";
	font-weight: normal;
	src:
    url("../fonts/didona/didona.woff") format("woff"), /* Modern Browsers */
		url("../fonts/didona/didona.woff2") format("woff2"); /* Modern Browsers */
	font-style: normal;
  font-display: swap;
}

